

@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

*{
    font-family: "Roboto", sans-serif;
    box-sizing: border-box;
    margin: 0;
}

.todobody{
    background-color: rgba(206, 201, 201, 0.685);
}


.sideNav{
  width: 35%;
  min-height: 100vh;
  padding: 10px;
  /* overflow: auto; */
  justify-content: space-between;
  display: flex;
  flex-direction: column;
  min-width: 300px;
}

.todoBoxBody{
  width: 100%;
}

.displayImg{
    margin: 50px auto 20px;
}

.displayImg > img{
    width: 100px;
    border-radius: 50%;
}


.userInfo{
    color: grey;
}

.pointsTable{
    background: #1e19b1;
    padding: 10px;
    margin: 40px 0;
    border-radius: 10px;
}

.container1{}
.container1 > label > span{font-size: 25px;}

.container2{}

.container2 > button{
    padding: 5px 7px;
    border-radius: 10px;
    font-size: 14px;

}


.user-options{
    border-radius: 10px;
    padding: 10px;
    margin: 25px 0 45px ;
}

.options{
    padding: 7px;
    transition: 0.3s;
    border-radius: 10px;
    cursor: pointer;
    margin-bottom: 10px;

}
.options:hover{

  background: darkblue;
  color: white;
}

.backgroundChange{
  background: darkblue;
  color: white;
}
.options > span{
    text-align: left;
}

.options > button , .roundbtn{
    padding: 3px 11px;
    border-radius: 15px;
    background:rgb(179, 176, 176);
    transition: 0.3s;
    
}
.options:hover > button{
    background:#c5cbc526 ;
}
.changeColor{
  background:#c5cbc526 !important;
}
.user-options > span{}

.coming-soon-btn{

}


/* mytask */
.todoBoxBody{
    /* background-color: rgba(206, 201, 201, 0.685); */
}

.header{
    border-bottom:solid 4px rgb(156, 154, 154);
}



.toggle-switch{
    background: #bdbbbbd6;
    padding: 2px;
    display: flex;
    /* width: auto; */
    border-radius: 10px;
    font-size: 13px;
    
    
}

.list{
    display: inline-block;
    word-spacing: 8px;
    padding: 2px 6px;
}
.board{
    display: inline-block;
    word-spacing: 8px;
    padding: 5px 14px;
    border-radius: 10px;
    background: white;
    color: darkblue;

}


.search{

    border-radius: 10px;
    padding: 2px 5px;
    box-shadow: blanchedalmond;
    background: white;
    box-shadow: -6px 5px 11px 0 rgba(0, 0, 0, 0.2);
}

.search > i{
    color: #999696;
}

.search > input{
    
    outline: none;
}

.header-option > select{
    outline: none;
    box-shadow: -6px 5px 11px 0 rgba(0, 0, 0, 0.2);
    border-radius: 10px;
    padding: 2px 7px;
    color: #999696;

}


.todoContainer{
    gap: 10px;
    padding: 20px 10px;
    background-color: rgba(206, 201, 201, 0.685);
}

.todoBox{
    width: 100%;
    display: flex;
    border-radius: 10px;
    flex-direction: column;

}
.todoheader{
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 20px;
    background: white;
    padding: 10px 10px 10px 0 ;
    border-radius: 5px;
}

.todoheader > span{
    padding: 2px 10px;
    border-left: solid 4px ;
}

.todos-box{
    background: white;
    padding: 10px;
    border-radius: 10px;
    margin: 0 20px 15px;
}
.todos-data{
    display: flex;
    justify-content: space-between;
    color: grey;
    font-size: 14px;
}
.todos-data1{
    display: flex;
    color: grey;
    justify-content: space-between;
    font-size: 14px;
}
.todoDate{}
.todo-level{}


/* myproject */


.card-container{
    gap: 20px;
}

.todoContainer{

}

.todoContainer > h1{
font-size: 30px;
font-weight: bold;
margin-bottom: 20px;
}

.card-todo{

background: white;
border-radius: 4px;
max-width: 450px;
}

.card-title{}

.card-title h4{
    font-size: 12px;
    font-weight: bold;
    color: grey;
}

.card-title > h5{
    font-weight: bold;
}

.cardheader > button{
    padding: 2px 4px;
    background: rgb(161, 160, 160);
    color: rgb(99, 98, 98);
    border-radius: 4px;
    /* font-size: 14px; */
    font-weight: bold;
}

.cardIcon{
    padding: 10px;
    background: lightskyblue;
    border-radius: 50%;
    color: white;
    margin-right: 14px;

}

.footerbtn1{    
    background: #9fb0b5;
    padding: 2px 4px;
    border-radius: 4px;
    color: #434141;
}
.footerbtn2{    background: #cda2a2;
    padding: 2px 4px;
    border-radius: 4px;
    color: red;}


.cardfooterBox1{
    font-size: 20px;
}

@media (max-width:1100px){
    .todoContainer {
        flex-wrap: wrap;
        row-gap: 40px;
    }
}


@media (max-width:1100px){

.todobody{
  flex-wrap: wrap;
}

.sideNav{
      width: 100%;
    min-height: auto;
}
}
@media (max-width:750px){
  .card-container{
    flex-wrap: wrap;
  }

  .card-todo{
    max-width: auto;
  }
}


@media (max-width:550px){
.user-options{
    font-size: 14px;
    
}
.header{
  flex-direction: column;
    align-items: flex-start;
    gap: 10px;
}
}




